.header-container{
    padding: 10px 15px;
}

#header-div{
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
}

#header-div img{
    width: 150px;
}

#header-div nav a{
    text-decoration: none;
    color: #000;
    font-size: 18px;
    padding: 5px;
    margin: 0 5px;
}

@media screen and (max-width: 768px){
    #header-div nav{
        padding: 0 20px;
        box-sizing: border-box;
    }
    
    
}

@media screen and (max-width: 550px){
    #header-div{
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: space-between;
        width: 100%;
    }

    #header-div nav{
        text-align: center;
        padding: 10px;
        margin-top: 10px;
        width: 100%;
        border-top: solid 1px #aaadaf;
    }
    
    
}