#contact-container{
    width: 100%;
    text-align: center;
    color: #fff;
    padding: 100px 0px;
    background-color: #05031b;
}

.contact-info{
    display: flex;
    justify-content: center;
}

.contact-content{
    display: flex;
    margin: 0 10px;
    flex-direction: column;
    justify-content: left;
    line-height: 15px;
}

.contact-detailed-content{
    display: flex;
    align-items: center;
    margin: 5px 0;
}

.contact-detailed-content img{
    width: 18px;
}

.contact-detailed-content span{
    font-size: 14px;
    margin-left: 10px;
}


@media screen and (max-width: 768px){
    #contact-container{
        box-sizing: border-box;
        padding: 60px 20px;
    }
}

@media screen and (max-width: 550px){
    #contact-container{
        box-sizing: border-box;
        padding: 10px;
    }

    .contact-info{
        display: flex;
        flex-direction: column;
        align-items: center;
    }

    .contact-content{
        display: flex;
        margin: 0 10px;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        line-height: 15px;
    }
    
    .contact-detailed-content{
        width: 150px;
    }
}